import React, { useEffect, useState } from 'react';
import axios from 'axios';
import {useHistory, withRouter, Link} from 'react-router-dom'
import { RootStateOrAny, useSelector } from 'react-redux';
import { PadWalletAddress } from 'utils/padwallet';
const BASE_URL = "https://my-json-server.typicode.com/themeland/netstorm-json/seller";

const Seller = () => {
    const [data, setData] = useState(null);
    const [sellerData, setSellerData] = useState([]);

    useEffect(() => {
        axios.get(`${BASE_URL}`)
            .then(res => {
                setData(res.data);
                setSellerData(res.data.sellerData);
                // console.log(data)
            })
        .catch(err => console.log(err))
    },[1])

    const sellers = useSelector((state: RootStateOrAny) => state.marketplace.sellers).sort((pre, next)=>next.amount - pre.amount);

    return (
        <section className="live-auctions-area">
            <div className="container">
                <div className="row">
                    <div className="col-12">
                        {/* Intro */}
                        <div className="intro m-0">
                            <div className="intro-content">
                                <span>{data?.preHeading}</span>
                                <h3 className="mt-3 mb-0">{data?.heading}</h3>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="row items">
                    {sellers.map((item, idx) => {
                        return (
                            <div key={`ts_${idx}`} className="col-12 col-sm-6 col-lg-4 item">
                                {/* Single Seller */}
                                <div className="card no-hover">
                                    <div className="single-seller d-flex align-items-center">
                                        <Link to={`user/${item?.wallet}`}>
                                            <img className="avatar-md rounded-circle" src={'/img/user.jpg'} alt="" />
                                        </Link>
                                        {/* Seller Info */}
                                        <div className="seller-info ml-3">
                                            <Link className="seller mb-2" to={`user/${item?.wallet}`}>{PadWalletAddress(item?.wallet)}</Link>
                                            <span>{item?.amount} KaiECO</span>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        );
                    })}
                </div>
            </div>
        </section>
    );
}

export default Seller;